.header_outer_container {
  position: sticky;
  top: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgb(20, 20, 20);
  background-color: transparent;
  height: 10vh;
  color: white;
  transition: all 0.5s;
  transition-timing-function: ease-in;
  z-index: 1;
  background-color: black;
}

.nav_black {
  background-color: rgb(20, 20, 20);
}
.header_continer {
  width: 90%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header_left ul {
  width: 140%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  list-style: none;
  left: none;
  cursor: pointer;
}

.header_right ul {
  width: 140%;
  display: flex;
  justify-content: space-between;
  list-style: none;
  align-items: center;
  left: none;
  cursor: pointer;
}
